"use client";
import { auth } from "@/app/firebase";
import { createUserWithEmailAndPassword, GoogleAuthProvider, signInWithEmailAndPassword, signInWithPopup } from "firebase/auth";
import Link from "next/link";
import {FaGoogle} from "react-icons/fa"
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import {
  useAuthState,
  useCreateUserWithEmailAndPassword,
  useSendEmailVerification,
} from "react-firebase-hooks/auth";
import { FaTruckLoading } from "react-icons/fa";

export default function Page() {
  const router = useRouter();

  const [loading, setLoading] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [user] = useAuthState(auth);
  const [message , setMessage] = useState("")

  
  useEffect(() => {
      if (user) {
        router.push("/career");
      }
  }, [user]);


  const onSubmit = async () => {
  try{
    setMessage("sign up in progress...")

    if (password && email){
    const result = await createUserWithEmailAndPassword(auth, email, password);
    }
    else{
      setMessage("password must be at least 8 characters and email must be valid")
      
    }
  
  }
  catch(e){
    setMessage("could not sign up ,please try again")
  }   
   
   if (result?.user.email){
   router.push("/career");}
    console.log(result)
  };
 
     
  


async function ongoogle() {
            const provider = new GoogleAuthProvider();
          try{
            const result = await signInWithPopup(auth, provider);
          }
          catch(e){
            setMessage("could not sign up with google ,try email instead")
          }
                if (result?.user.email){
   router.push("/career");}
     }

  return (
    <div className="flex justify-center items-center flex-col pt-40">
{/* background objects */}
<div className="fixed bg-red-100 top-40 right-19 size-40 min-md:opacity-25 max-md:hidden flex justify-center items-center font-serif rounded-xl animate-r1 delay-100 border-green-400/60 border">learn</div>
<div className="fixed bg-green-100 top-42 left-19 size-40 min-md:opacity-20 max-md:hidden flex justify-center items-center font-serif rounded-xl animate-l1 delay-200 border-red-200/60 border">grow</div>
    <div className="fixed bg-yellow-100 bottom-8 left-[42rem] size-40 min-md:opacity-27 max-md:hidden flex justify-center items-center font-serif rounded-xl animate-l1 border-red-200/60 border">improve</div>
    
<div className="fixed min-md:bottom-32 bg-red-100 left-19 size-40 min-md:opacity-70 max-md:opacity-40 flex justify-center items-center font-serif rounded-xl animate-r1 border-green-400/60 border">learn</div>
<div className="fixed min-md:bottom-28 bg-green-100 right-19 size-40 min-md:opacity-70 max-md:opacity-40 flex justify-center items-center font-serif rounded-xl animate-l1 border-red-200/60 border">grow</div>
 

{/* form */}
  <h1 className="font-[1.1rem] font-sans opacity-70">Welcome to</h1>
      <div className="flex font-[1.1rem] mb-20 font-sans border-blue-400/70 px-5 rounded-2xl scale-150 animate-fade border-dashed">  <div className="flex mx-4  z-40 opacity-100 text-black/90 text-4xl p-3 font-serif font-extrabold"><h1 className="text-blue-500/90">Studi</h1><h1>Pal</h1></div>
         </div>
      {loading ? (
        <FaTruckLoading className="animate-spin w-8 h-8" />
      ) : (
        <div className="flex justify-center items-center flex-col">
         <form className="flex justify-center items-center gap-6 mb-10">
             <input
             required
            type="text"
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            placeholder="Email"
            className="text-xl px-4 cursor-pointer py-2 rounded-md border border-gray-300 mb-4  hover:scale-105 duration-300"
          />
          <input
          required
            type="text"
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            placeholder="Password"
            className="text-xl px-4 cursor-pointer py-2 rounded-md border border-gray-300 mb-4 hover:scale-105 duration-300 "
          />


         </form>
                      <button
            className="bg-yellow-500 opacity-85 text-black cursor-pointer hover:scale-105 px-4 py-2 rounded-md font-bold mb-10"
            onClick={onSubmit}
          >
            SIGN UP
          </button>
          <p className="text-amber-600/80 p-1 m-2">{message}</p>
          
           <Link  href="login" className="border-2 cursor-pointer animate-fade2 duration-300 max-md:scale-70 hover:bg-blue-200/40 mb-2 hover:opacity-90 hover:scale-105 border-blue-200 ring-1 ring-black/40 p-1 rounded-2xl opacity-60">Already have an account?</Link>
                     <h1 className="m-5 animate-fade2">or</h1>
           <div onClick={ongoogle} className="cursor-pointer bg-blue-500 scale-70 animate-fade2 opacity-60 hover:opacity-80 hover:scale-90 duration-300  text-white px-4 py-2 rounded flex gap-2">Sing up with Google<div className="my-2 "><FaGoogle/></div></div>
      
        </div>
      )}
    </div>
  );
}